.about-logo-container {
	display: flex;
	justify-content: left;
	padding-top: 25px;
}

.about-logo {
	display: flex;
	position: fixed;
	border: 1px solid white;
	border-radius: 50%;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
	top: 4vh;
}

.about-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding-top: 120px;
}

.about-main {
	display: flex;
}

.about-right-side {
	flex-basis: 900px;
}

.about-title {
	opacity: 0;
	transform: translateY(-30px);
	width: 100% !important;
}

.fade {
  	animation: fadeInDown 0.3s ease-in-out forwards;
}

.about-subtitle {
	opacity: 0;
	transform: translateY(-30px);
	width: 80% !important;
}

.about-subtitle2 {
	opacity: 0;
	transform: translateY(-30px);
	width: 80% !important;
}

.about-left-side {
	display: flex;
	flex-direction: column;
	flex-basis: 300px;
}

.about-image-container {
	opacity: 0;
	width: 370px;
}

.about-image-wrapper {
	position: relative;
	display: inline-block;
	overflow: hidden;
	width: 100%;
	border-radius: 10%;
	
	/* transform: rotate(3deg); */
}

.about-image-wrapper .hover-text {
	position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent background for better visibility */
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 16px;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out;
    pointer-events: none;
  }

  .about-image-wrapper:hover img {
    filter: blur(5px); /* Apply blur effect to the image on hover */
}

  .about-image-wrapper:hover .hover-text {
	visibility: visible;
	opacity: 1;
  }

.about-image-wrapper img {
	display: block;
	width: 100%;
	transition: filter 0.3s ease-in-out;
}


.about-socials {
	padding-top: 50px;
	padding-bottom: 50px;
}

.about-socials-mobile {
	display: none;
}

@media (max-width: 1024px) {
	.about-logo {
		z-index: 100;
	}

	.about-main {
		flex-direction: column;
	}

	.about-subtitle {
		padding-top: 20px !important;
	}
	.about-subtitle2 {
		padding-top: 20px !important;
	}

	.about-left-side {
		flex-basis: 100%;
		order: 1;
	}

	.about-right-side {
		flex-basis: 100%;
		order: 2;
	}

	.about-image-container {
		padding-bottom: 40px;
	}

	.about-socials {
		display: none;
	}

	.about-socials-mobile {
		padding-top: 30px;
		display: flex;
	}
}

@media (max-width: 600px) {
	.about-container {
		padding-top: 60px;
	}

	.about-image-container {
		width: 80%;
	}
}


@keyframes fadeInDown {
	from {
	  opacity: 0;
	  transform: translateY(-30px); /* Start position */
	}
	to {
	  opacity: 1;
	  transform: translateY(0); /* End position */
	}
  }
  
